//
// Site Header
//
.site-nav {
  background: $md-sys-elevation-level2;
  height: 80px;
  width: 100%;
  z-index: 100;
  .nav-links {
    float: right;
    margin-right: 25px;
    margin-left: 0;
  }
  a {
    line-height: 80px;
    margin: 0 5px;
    color: $md-sys-color-on-surface-dark;
    font-size: 17px;
    padding: 7px 13px;
    border-radius: 3px;
    text-transform: uppercase;
    transition: $transition-links;

    &:hover {
      color: $md-ref-palette-secondary-100;
    }

    // Download btn
    &:first-child {
      @include btn;
      padding: 11px 19px 13px;
      background-color: $md-sys-color-secondary-dark;
      color: $md-sys-color-on-secondary-dark;

      &:hover {
        background: $md-sys-color-secondary-dark;
      }
    }
  }

  .logo {
    text-transform: none;
    color: $text-color;
    font-size: 35px;
    line-height: 80px;
    padding: 0 13px;
    font-weight: bold;

    &:hover {
      //CHANGE: hover state
      color: $text-mute-color;
      background-color: transparent;
    }

    @media screen and (min-width: $pc-width) {
      padding: 0 30px;
    }
  }
  .checkbtn {
    font-size: 40px;
    color: $md-sys-color-primary-dark;
    float: right;
    top: 20px;
    // line-height: 80px;
    cursor: pointer;
    display: none;
    margin: 1% 20px 1% 0;
    padding: 10px;
    border-radius: 43px;
    background: $md-sys-elevation-level4;
    transition: background 0.3s ease-in-out;
    

    &:hover {
      background: $md-sys-elevation-level5;
    }
  }
  #check {
    display: none;
  }
  @media (max-width: 885px) {
    .checkbtn {
      display: block;
    }
    .nav-links {
      position: fixed;
      width: 100%;
      height: 100vh;
      background: $md-sys-color-surface-dark;
      top: 80px;
      left: 100%;
      text-align: center;
      transition: all 0.5s;
      a {
        display: block;
        margin: 0;
        font-size: 20px;

        &:first-child {
          background-color: transparent;
          border-radius: 3px;
          color: $md-sys-color-primary-dark;
        }

        &:hover {
          background: $md-sys-elevation-level5;
          color: $md-sys-color-primary-dark;
        }
        &.active {
          background: $btn-bg-color;
        }
      }
    }
    #check {
      &:checked {
        ~ {
          .nav-links {
            left: 0;
          }
        }
      }
    }
  }
}

//
// Header Banner
//
.header-home-mod {
  position: static;
  background: $md-sys-elevation-level2;
}
.site-banner {
  padding: 40px 15px 40px 15px;
  color: $text-color;
  background: $md-sys-elevation-level2;
  text-align: center;
  justify-content: space-around;
  line-height: 1.5;

  .img {
    padding-top: 61px;
    width: 100%;

    > img {
      @include animation-fade-in(2.1);
      box-shadow: 0px 0px 20px 0px #00000052;
      transition: all 300ms ease 0ms;

      &:hover {
        box-shadow: 0px 0px 0px 0px #00000052;
      }
    }
  }

  .banner-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    > a {
      display: block;
      margin-top: 30px;
      padding: 15px 81px;
      @include btn;
      @include animation-fade-in(1.65);
    }

    .page-header-title {
      border-top: 8px;
      margin-bottom: 0%;
      font-size: $md-sys-typescale-display-large-size;
      color: $md-sys-color-on-surface-dark;
      text-align: center;
      font-weight: 700;
      font-family: $font-family;
      padding-bottom: 7px;
      line-height: 1.5;
      @include animation-fade-in(0.1);
    }

    .site-banner-description {
      border-top: 8px;
      margin-bottom: 0%;
      font-size: $md-sys-typescale-body-large-size;
      color: $md-sys-color-on-surface-dark;
      text-align: center;
      font-family: $font-family;
      padding-bottom: 7px;
      @include animation-fade-in(1);
    }
  }
  // For Large Screens
  @media screen and (min-width: $mobile-width) {
    padding-bottom: 100px;

    .banner-content {
      > a {
        display: none;
      }
    }
  }

  @media screen and (min-width: 993px) {
    display: flex;
    padding: 40px 10px 140px 10px;

    .img {
      width: $site-banner-picture-sze_wdth;

      > img {
        @include animation-fade-in(2.65);
      }
    }

    .banner-content {
      transform: translateX(30vw);
      animation: slide 1250ms cubic-bezier(0.75, 0, 0.33, 1) forwards;
      animation-delay: 2s;

      > a {
        display: block;
      }
    }
  }
}



.text-muted {
  @include text-muted;
}

.text-unmuted {
  @include text-unmuted;
}

// 
// Posts
// 
.post-header {
  margin-bottom: $font-spacing;
}

.post-title {
  //CHANGE: include display-large mixin 
  font-family: var(--md-sys-typescale-display-large-font);
  font-weight: 700;
  font-size: var(--md-sys-typescale-display-large-size);
  font-style: var(--md-sys-typescale-display-large-font-style);
  letter-spacing: var(--md-sys-typescale-display-large-tracking);
  line-height: var(--md-sys-typescale-display-large-line-height);
  text-transform: var(--md-sys-typescale-display-large-text-transform);
  text-decoration: var(--md-sys-typescale-display-large-text-decoration);
  line-height: 1.15;
  margin-bottom: 5px;

  @media screen and (min-width: $pc-width) {
    @include relative-font-size(3.7);
  }
}
.post-list {
  padding: 9px;
  margin-left: 0;
  list-style: none;

  .post {
    margin-bottom: $font-spacing;
    padding: 20px 30px 20px 30px;
    background: $md-sys-elevation-level2;
    border-radius: 40px;
    transition-timing-function: cubic-bezier(
      $md-sys-motion-easing-emphasized-x0,
      $md-sys-motion-easing-emphasized-y0,
      $md-sys-motion-easing-emphasized-x1,
      $md-sys-motion-easing-emphasized-y1);
    transition: $transition-btn;

    &:hover {
      border-radius: 10px;
      background: $md-sys-elevation-level5;
    }

    .post-meta {
      font-size: $small-font-size;
      color: $text-color;
      word-spacing: 1px;
    }
    .post-discription {
      .post-link,
      a {
        font-size: $font-size;
        line-height: 5px;
        color: $md-sys-color-on-background-dark;
      }
    > p {
      //  Make the description shorter
      margin: 0;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; // line height
      -webkit-box-orient: vertical;
    }

    > h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-size: $font-size;
      line-height: 1;
      color: $md-sys-color-on-background-dark;
    }      
    }



  }
  .post-link {
    width: 50rem;
    //CHANGE: include display-large mixin 
    font-family: var(--md-sys-typescale-display-large-font);
    font-weight: 500;
    font-size: var(--md-sys-typescale-display-large-size);
    font-style: var(--md-sys-typescale-display-large-font-style);
    letter-spacing: var(--md-sys-typescale-display-large-tracking);
    line-height: var(--md-sys-typescale-display-large-line-height);
    text-transform: var(--md-sys-typescale-display-large-text-transform);
    text-decoration: var(--md-sys-typescale-display-large-text-decoration);
    color: $md-sys-color-primary-dark;
  }
}

.post-info {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
  cursor: default;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
    font-size: $font-size;
    line-height: 5px;
    color: $md-sys-color-on-background-dark;

  

  .post-info-date {
    color: $md-sys-color-secondary-dark;
    line-height: 0.99;
    display: flex;

    &::before {
      word-spacing: 1;
      padding: 0px 5px 0px 0px;
      content: "calendar_today";
      font-family: "Material Symbols Rounded";
      font-variation-settings:
      'FILL' 1,
      'wght' 700,
      'GRAD' 1,
      'opsz' 48;
    }
  }

  .post-info-pin {
    background-color: $md-sys-color-secondary-dark;
    color: $md-sys-color-on-secondary-dark;
    border-radius: 40px;
    padding: 2px 5px 2px 5px;
    cursor: default;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    line-height: 0.99;
    display: flex;

    > span {
      transform: rotate(45deg);
      font-size: 15px;
      font-weight: 700;
      line-height: 1.4rem;
      height: 1.3rem;
    }

    > p {
      margin-bottom: 0;
      display: none;
      text-transform: capitalize;
      font-weight: 700;
    }

    @media screen and (min-width: $mobile-width) {
      > p {
        display: inline-block;
      }
    }
  }
}

//
// Site footer
//
.site-footer {
  background: $md-sys-elevation-level2;
  padding-top: $font-spacing;
  padding-bottom: 10px;

  .site-footer-wrapper {
    display: flex;
    flex-direction: column-reverse;
  }

  .footer-col {
    list-style: none;
    font-size: 20px;
    margin-bottom: 10px;

    .links-footer {
      width: calc(100% - (#{$font-spacing} / 2));
      transition: $transition-links;

      &:first-child {
        //CHANGE: include headline-large mixin 
        font-family: var(--md-sys-typescale-headline-large-font);
        font-weight: 700;
        font-size: var(--md-sys-typescale-headline-large-size);
        font-style: var(--md-sys-typescale-headline-large-font-style);
        letter-spacing: var(--md-sys-typescale-headline-large-tracking);
        line-height: var(--md-sys-typescale-headline-large-line-height);
        text-transform: var(--md-sys-typescale-headline-large-text-transform);
        text-decoration: var(--md-sys-typescale-headline-large-text-decoration);
        text-transform: uppercase;
        margin-bottom: 10px;
        color: $md-sys-color-primary-dark;
        font-variation-settings: "wght" 600;
        transition: font-variation-settings 200ms cubic-bezier(0.2, 0, 0, 1);
      }

      &:not(:first-child) {
        padding: 2px 0;
      }

      > a {
        color: $md-sys-color-primary-dark;
        font-weight: 500;

        &:visited {
          color: $md-sys-color-primary-dark;
        }

        &:hover {
          color: $md-ref-palette-primary-60;
          font-weight: 600;
        }
      }
    }

    .footer-product {
      list-style: none;
      color: $md-ref-palette-primary-60;
      width: calc(100% - (#{$font-spacing} / 2));
      padding: 0 ($font-spacing / 2);

      &:first-child {
        padding: 20px 0px 0px;
        //CHANGE: include display-large mixin 
        font-family: var(--md-sys-typescale-display-small-font);
        font-weight: 900;
        font-size: var(--md-sys-typescale-display-small-size);
        font-style: var(--md-sys-typescale-display-small-font-style);
        letter-spacing: var(--md-sys-typescale-display-small-tracking);
        line-height: var(--md-sys-typescale-display-small-line-height);
        text-transform: var(--md-sys-typescale-display-small-text-transform);
        text-decoration: var(--md-sys-typescale-display-small-text-decoration);
      }

      &:hover {
        color: $md-ref-palette-primary-60;
        text-decoration: none;
      }
    }
  }

  @media screen and (min-width: 1000px) {
    .site-footer-wrapper {
      flex-direction: row;
    }

    .footer-col {
      &:not(:first-child) {
        @include footer-wrapper;
      }

      &:first-child {
        @include footer-wrapper;
        width: calc(42% - (#{$font-spacing} / 2));
      }
    }
  }
}
.footer-info {
  padding-top: 10px;
  border-top: 1px solid $md-sys-color-outline-dark;
  font-size: $md-sys-typescale-body-small-size;
  justify-content: space-between;
  font-weight: $md-sys-typescale-body-small-weight;
  display: flex;
  flex-direction: column;
  padding: 10px 10px 0;
  margin-left: 0px;
  margin-right: 0px;

  > span {
    margin-bottom: 3px;

    > a {
      color: #9e9e9e;

      &:visited {
        color: #9e9e9e;
      }

      &:hover {
        color: $text-color;
      }
    }
  }

  @media screen and (min-width: 692px) {
    flex-direction: row;
    margin-left: 100px;
    margin-right: 100px;
  }
}

//
// Post Nav
//

.post-nav {
  padding-top: 5px;
  display: inline;
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  gap: 80px;
  -ms-flex-align: center;
  align-items: center;
  margin: 0px;

  @media screen and (min-width: $mobile-width) {
    gap: 100px;
  }
}

.post-nav-svg {
  //CHANGE: Add material theme
  color: $footer-links-color;
  transition: $tansitions-page-bar;
  padding: 8px;
  border-radius: 50px;

  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.disable-svg {
  cursor: not-allowed;
  color: $text-mute-color;
}

.post-nav-num {
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
  cursor: default;
  display: inline;
  @include text-muted;
}

//
// Post footer
//
.post-footer {
  //CHANGE: Add material theme
  padding-top: 13px;
  border-top: 3px solid $border-color;
  margin: 0px;
}
